@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

*{
    padding: 0;
    margin: 0;
  }


@mixin mobile {
    @media screen and (max-width:480px){
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width:768px){
        @content;
    }
}
@mixin mobile {
    @media screen and (max-width:1200px){
        @content;
    }
}

.formContainer {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative; // Ensures the form container is positioned relative to its children
    z-index: 1; // Ensures the form container is above the waves
    .formWrapper {
        background-color: rgba(225, 196, 196,0.6);
        border-radius: 50px;
        padding: 40px 80px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;

        img{
            height: 3rem;
        }

        .logo{
            color: #f6f6f6;
            font-weight: bold;
            font-size: 2rem;
        }

        .title{
            color: #f6f6f6;
            font-weight: bold;
            font-size: 1.2rem;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 15px;

            label{
                cursor: pointer;
                display: flex;
                align-items: center;
                color: #315799;
                font-size: 1rem;

                img{
                    height: 2.4rem;
                }
                span{
                    margin-left: 10px;
                    font-size: 0.7rem;
                    font-weight: 500;
                }
            }

            input{
                color: #f6f6f6;
                background-color: rgba(0,0,0,0);
                padding: 10px;
                border: none;
                outline: none;
                width: 250px;
                border-bottom: 1px solid #f6f6f6;
                &::placeholder{
                    color: #f6f6f6;
                }
            }

            button{
                border: none;
                outline: none;
                border-radius: 13px;
                font-weight: bold;
                padding: 10px;
                color: #f6f6f6;
                cursor: pointer;
                background-color: rgba(217, 83, 83,0.8);
            }

        }
        
        p{
            color: #3f6f96;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

body {
    margin: auto;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    overflow: auto;
    background: linear-gradient(315deg, #f65b6a 10%, #7caacc 38%, #b649d4 68%, #f4cc5f 98%);
    animation: gradient 15s ease infinite;
    background-size: 400% 400%;
    background-attachment: fixed;
}

@keyframes gradient {
    0% {
        background-position: 0% 0%;
    }
    50% {
        background-position: 100% 100%;
    }
    100% {
        background-position: 0% 0%;
    }
}

.wave {
    background: rgb(255 255 255 / 25%);
    border-radius: 1000% 1000% 0 0;
    position: fixed;
    width: 200%;
    height: 12em;
    animation: wave 10s -3s linear infinite;
    transform: translate3d(0, 0, 0);
    opacity: 0.8;
    bottom: 0;
    left: 0;
    z-index: -1; // Ensures the waves are below the form container
}

.wave:nth-of-type(2) {
    bottom: -1.25em;
    animation: wave 18s linear reverse infinite;
    opacity: 0.8;
}

.wave:nth-of-type(3) {
    bottom: -2.5em;
    animation: wave 20s -1s reverse infinite;
    opacity: 0.9;
}

@keyframes wave {
    2% {
        transform: translateX(1);
    }

    25% {
        transform: translateX(-25%);
    }

    50% {
        transform: translateX(-50%);
    }

    75% {
        transform: translateX(-25%);
    }

    100% {
        transform: translateX(1);
    }
}

.home{
    background: linear-gradient(to right, #346fcf, #7caacc );
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container{
        overflow: hidden;
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;

        @include tablet{
            width: 90%;
        }

        .sidebar{
            flex: 1;
            background-color: #3e3c61;
            position: relative;

            .nav{
                display: flex;
                align-items: center;
                background-color: #2f2d52;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: #ddddf7;

                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }

                .user{
                    display: flex;
                    gap: 10px;

                    img{
                        background-color: #ddddf7;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    button{
                        background-color: #5d5b8d;
                        color: #ddddf7;
                        border: none;
                        outline: none;
                        padding: 5px;
                        cursor: pointer;
                        @include tablet{
                            position: absolute;
                            bottom: 10px;
                        }
                    }
                }
                span{
                    font-size: 1rem;
                    img{
                        height: 2rem;
                    }
                }
            }
            .search{
                border: none;
                border-bottom: 1px solid gray;

                .searchForm{
                    padding: 10px;
                    input{
                        background-color: transparent;
                        color: #ddddf7;
                        border: none;
                        outline: none;
                        &::placeholder{
                            color: #ddddf7;
                        }
                    }
                }
            }

            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: #ddddf7;
                cursor: pointer;

                &:hover{
                    background-color: #2f2d52;
                }

                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .userChatInfo{
                    span{
                        font-size: 18px;
                        font-weight: 500;
                    }

                    p{
                        white-space: pre-wrap;
                        overflow-wrap: anywhere;
                        font-size: 14px;
                        color: #9e9ea9;
                    }
                }
            }
        }

        .chat{
            flex: 2;
            .chatInfo{
                overflow: auto;
                height: 50px;
                background-color: #5d5b8d;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;

                span{
                    color: #ddddf7;
                    font-size: 1.2rem;
                    font-weight: 500;
                }
                .chatIcons{
                    display: flex;
                    gap: 10px;
                    
                    img{
                        cursor: pointer;
                        height: 1.5rem;
                    }
                }
            }

            .messages{
                background-color: #e9dca9;
                padding: 10px;
                height: calc(100% - 160px);
                overflow: auto;

                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;

                    .messageInfo{
                        font-size: 10.9px;
                        display: flex;
                        flex-direction: column;
                        color: #9e9ea9;
                        font-weight: 300;
                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                        }
                    }
                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        
                        p{
                            white-space: pre-wrap;
                            overflow-wrap: anywhere;
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }

                        img{
                            width: 50%;
                        }
                    }

                    &.owner{
                        flex-direction: row-reverse;

                        .messageContent{
                            align-items: flex-end;

                            p{
                                background-color: #6c83ce;
                                color: rgb(255, 248, 248);
                                border-radius: 10px 0px 10px 10px;
                            }
                        }
                    }
                }
            }

            .input{
                height: 50px;
                background-color:#e2edf8;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                input{
                    height: 100%;
                    width: 100%;
                    border: none;
                    outline: none;
                    color: #2f2d52;
                    font-size: 16px;

                    &::placeholder{
                        color: #5d5b8d;
                    }
                }

                input[type="text"] {
                    background-color:#e2edf8;
                   }

                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img{
                        height: 1.5rem;
                        cursor: pointer;
                    }
                    label{
                        img{
                            height: 1.5rem;
                            cursor: pointer;
                        }
                    }

                    button{
                        cursor: pointer;
                        border: none;
                        outline: none;
                        background-color: #8da4f1;
                        color: #f6f6f6;
                        padding: 10px 15px;
                        border-radius: 5px;
                    }
                }
            }
        }
    }
}